'use client'

import Link from 'next/link'
import { usePathname } from 'next/navigation'

export default function DashboardShell({ children, title = 'Dashboard' }: { children: React.ReactNode; title?: string }) {
  const pathname = usePathname()
  const nav = [
    { href: '/admin/dashboard', label: 'Dashboard' },
    { href: '/admin/users', label: 'Users' },
    { href: '/admin/businesses', label: 'Businesses' },
    { href: '/admin/visibility', label: 'Visibility' },
    { href: '/admin/investments', label: 'Investments' },
  ]

  return (
    <div className="min-h-screen bg-background text-foreground">
      <div className="flex">
        <aside className="w-64 bg-sidebar text-sidebar-foreground p-4 border-r border-sidebar-border">
          <div className="flex items-center gap-2 mb-6">
            <div className="w-8 h-8 rounded-lg bg-sidebar-primary" />
            <span className="font-semibold">Nebula Hub</span>
          </div>
          <nav className="space-y-1">
            {nav.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className={`block px-3 py-2 rounded-lg ${
                  pathname === item.href ? 'bg-sidebar-primary text-sidebar-primary-foreground' : 'hover:bg-sidebar-accent'
                }`}
              >
                {item.label}
              </Link>
            ))}
          </nav>
        </aside>

        <main className="flex-1">
          <header className="border-b border-border bg-card">
            <div className="max-w-7xl mx-auto px-6 py-5 flex items-center justify-between">
              <h1 className="text-2xl font-bold">{title}</h1>
              <div className="flex items-center gap-3">
                <input
                  placeholder="Search"
                  className="w-72 px-4 py-2 rounded-lg bg-secondary text-foreground placeholder-muted-foreground border border-border"
                />
                <button className="px-4 py-2 rounded-lg bg-primary text-primary-foreground">Export Data</button>
              </div>
            </div>
          </header>
          <section className="max-w-7xl mx-auto px-6 py-8">{children}</section>
        </main>
      </div>
    </div>
  )
}

