"use client"

import { useSession } from "next-auth/react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { useEffect, useState } from "react"
import DashboardShell from "@/components/dashboard-shell"

export default function AdminDashboard() {
  const { data: session, status } = useSession()
  const router = useRouter()
  const [stats, setStats] = useState<{
    users: { total: number }
    businesses: { total: number; pending: number; approved: number; rejected: number }
    investments: { total: number }
  } | null>(null)
  const [loadingStats, setLoadingStats] = useState(true)

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login")
    }
  }, [status, router])

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const res = await fetch("/api/admin/stats")
        if (res.ok) {
          const data = await res.json()
          setStats(data.data)
        }
      } catch (e) {
        // ignore
      } finally {
        setLoadingStats(false)
      }
    }
    if (status === "authenticated") fetchStats()
  }, [status])

  if (status === "loading") {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>
  }

  return (
    <DashboardShell title="Dashboard">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <div className="bg-card rounded-xl border border-border p-6">
          <p className="text-sm text-muted-foreground">Total Users</p>
          <p className="text-3xl font-bold mt-2">{loadingStats ? "—" : stats?.users.total ?? 0}</p>
        </div>
        <div className="bg-card rounded-xl border border-border p-6">
          <p className="text-sm text-muted-foreground">Businesses</p>
          <p className="text-3xl font-bold mt-2">{loadingStats ? "—" : stats?.businesses.total ?? 0}</p>
          <div className="mt-3 text-xs text-muted-foreground flex gap-3">
            <span>Pending: {loadingStats ? "—" : stats?.businesses.pending ?? 0}</span>
            <span>Approved: {loadingStats ? "—" : stats?.businesses.approved ?? 0}</span>
            <span>Rejected: {loadingStats ? "—" : stats?.businesses.rejected ?? 0}</span>
          </div>
        </div>
        <div className="bg-card rounded-xl border border-border p-6">
          <p className="text-sm text-muted-foreground">Total Investments</p>
          <p className="text-3xl font-bold mt-2">{loadingStats ? "—" : stats?.investments.total ?? 0}</p>
        </div>
        <div className="bg-card rounded-xl border border-border p-6">
          <p className="text-sm text-muted-foreground">Welcome</p>
          <p className="mt-2">{session?.user?.name}</p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2 bg-card rounded-xl border border-border p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold">Vendor movements</h2>
            <div className="flex gap-2">
              <button className="px-3 py-1 rounded-lg bg-secondary">12 Month</button>
              <button className="px-3 py-1 rounded-lg bg-secondary">6 Month</button>
              <button className="px-3 py-1 rounded-lg bg-secondary">30 Days</button>
            </div>
          </div>
          <div className="h-56 rounded-lg bg-secondary" />
        </div>
        <div className="bg-card rounded-xl border border-border p-6">
          <h2 className="text-lg font-semibold mb-4">Recent Customers</h2>
          <div className="space-y-3">
            <div className="flex items-center justify-between"><span>Oliver Hayes</span><span className="text-green-500">$8</span></div>
            <div className="flex items-center justify-between"><span>Michael Brown</span><span className="text-green-500">$24</span></div>
            <div className="flex items-center justify-between"><span>Emily Davis</span><span className="text-green-500">$8</span></div>
          </div>
        </div>
      </div>
    </DashboardShell>
  )
}
